module CompileWorker

open Feliz.UseWorker
open TransmuteLib

let compileRules' (features: Map<string, Node>, sets: Map<string, Node>, rules: (int * Node) list) =
    try
        rules
        |> List.map (fun (i, rule) -> i, RuleCompiler.compile false features sets rule)
        |> Ok
    with
    | ex ->
        Result.Error (ex.Message + "\n" + ex.StackTrace)

let compileRules = WorkerFunc.Create("CompileWorker", "compileRules", compileRules')
